.modal_item_body {
  font-size: 12px;
  width: 100%;
}
.modal_item_body .modal_item_header {
  width: 100%;
  padding: 0px 21px;
  height: 42px;
  line-height: 42px;
  background: #F2F9FF;
  border: 1px solid #e0efff;
  border-radius: 2px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 400;
}
.modal_item_body .modal_item_header .modal_item_header_icon {
  color: var(--theme-color);
  margin-right: 9px;
  font-size: 14px;
}
.modal_item_body .modal_item_body_tips {
  padding: 0px 54px 0px 44px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
  margin-top: 28px;
}
.modal_item_body .modal_item_body_info {
  margin-top: 20px;
  padding: 0px 90px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li {
  width: 70px;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-right: 50px;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li .modal_item_body_info_li_img {
  width: 60px;
  height: 60px;
  margin-bottom: 12px;
  border: 1px solid #979797;
  border-radius: 100%;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li .modal_item_body_info_li_img .modal_item_body_info_li_img_content {
  width: 100%;
  height: 100%;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li .modal_item_body_info_li_text {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 600;
  width: inherit;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li_check .modal_item_body_info_li_img {
  position: relative;
  border: 1px solid var(--theme-color);
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li_check .modal_item_body_info_li_text {
  color: var(--theme-color);
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li_check .modal_item_body_info_li_check_icon {
  border-radius: 100%;
  color: var(--theme-color);
  text-align: center;
  justify-items: center;
  position: absolute;
  top: 0px;
  right: 0px;
  line-height: 14px;
  background-color: #fff;
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li:hover {
  color: var(--theme-color);
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li:hover .modal_item_body_info_li_img {
  border: 1px solid var(--theme-color);
}
.modal_item_body .modal_item_body_info .modal_item_body_info_li:hover .modal_item_body_info_li_text {
  color: var(--theme-color);
}
